@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,400;0,500;0,600;0,700;0,800;0,900;1,300&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
		"Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
		"Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	scrollbar-width: 0px;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}


/* color: #379bf9;
color: #9bdc28; */

body {
	margin: 0;
	padding: 0;
	background: #333333;
	color: #ffffff;
	box-sizing: border-box;
	scrollbar-width: none;
}

body::-webkit-scrollbar {
	display: none;
}

.dataset h2 {
	text-align: center;
	font-family: "Poppins", sans-serif;
	font-weight: 200;
}

#grid24 {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-rows: repeat(2, 1fr);
	grid-gap: 10px;
	padding: 10px;
}

#grid24 .widgetElement {
	display: flex;
}

#grid24 .widgetElement:nth-child(1) {
	grid-column: 1/2;
	grid-row: 1/2;
}

#grid24 .widgetElement:nth-child(2) {
	grid-column: 2/3;
	grid-row: 1/2;
}

#grid24 .widgetElement:nth-child(3) {
	grid-column: 1/2;
	grid-row: 2/3;
}

#grid24 .widgetElement:nth-child(4) {
	grid-column: 2/3;
	grid-row: 2/3;
}

#grid24 .widgetElement:nth-child(5) {
	grid-column: 3/5;
	grid-row: 1/3;
}

#grid12 {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-template-rows: repeat(1, 1fr);
	grid-gap: 10px;
	padding: 10px;
}

#grid12 .widgetElement {
	display: flex;
}

#grid12 .widgetElement:nth-child(1) {
	grid-column: 1/2;
	grid-row: 1/2;
}

#grid12 .widgetElement:nth-child(2) {
	grid-column: 2/3;
	grid-row: 1/2;
}

.description {
	font-family: "Poppins", "sans-serif";
	text-align: center;
	color: #ffffff;
	margin-bottom: 50px;
}

.description .schema {
	margin: 0px 50px 50px 50px;
}

.description h1 {
	margin: 20px 20px 0px 20px;
	font-size: 80px;
	font-weight: 300;
}

.description p {
	margin: 0 50px 50px 50px;
	font-weight: 200;
}

.description .schema h4 {
	display: inline-block;
	font-weight: 200;
}

.description .schema div {
	display: inline-block;
	padding: 0px 15px;
	border-radius: 15px;
	margin: 0px 5px;
	font-weight: 600;
	font-size: 15px;
}

.description .schema #date-and-time {
	background: #ffd600;
	color: #333333;
}

.description .schema #customer-info {
	background: #f57f17;
	color: #333333;
}

.description .schema #shop-info {
	background: #379bf9;
	color: #333333;
}

.description .schema #product-info {
	background: #9bdc28;
	color: #333333;
}

.widgetWrap {
	text-align: center;
	font-family: "Nunito", sans-serif;
	background: #222222;
	width: 100%;
	height: 100%;
	border-radius: 5px;
}

.widgetWrap .widgetTitle {
	font-weight: 400;
	font-size: 20px;
	text-align: center;
	text-transform: uppercase;
	padding: 20px 10px;
	color: #ffffff;
}

.widgetWrap .widgetValue .value {
	font-weight: 700;
	font-size: 65px;
	text-align: center;
	padding: 0px 5px;
	color: #379bf9;
}

.widgetWrap .widgetValue .valueDescription {
	font-weight: 300;
	font-size: 14px;
	padding: 0px 5px 20px 5px;
	text-align: center;
	color: #cccccc;
}

.bar {
	box-sizing: border-box;
	font-family: "Nunito", sans-serif;
	background: #222222;
	width: 100%;
}

.line {
	box-sizing: border-box;
	font-family: "Nunito", sans-serif;
	background: #222222;
	width: 100%;
}

.doughnut {
	box-sizing: border-box;
	font-family: "Nunito", sans-serif;
	background: #222222;
	width: 100%;
}

